$dark: #6ba3a5;
$middle: #bdd7d7;
$light: #dbecef;
$glow: #6ba3a5;
$accent: #e6b085;

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
	scroll-behavior: smooth;
	width: 100vw;
	body {
		width: 100vw;
	}
}
body {
	background-color: #1a2229;
	color: #b7b7b7;
	font-family: 'Martel Sans', sans-serif;
	overflow-x: hidden;
}
footer {
	padding: 15px;
}
.oval_container {
	overflow-x: hidden;
}

.hide_xs {
  display: none;
  @media (min-width: 576px) {
    display: inline-block;
  }
}

.oval {
	position: absolute;
	top: 100vh;
	left: -500px;
	width: 3000px;
	height: 100vh;
	background: black;
	border-radius: 3000px / 50vh;
	transform: rotateZ(10deg);
	box-shadow: 0 0 30px 20px #000;
	z-index: -9999;
}
#logo {
	width: 100%;
	display: flex;
	justify-content: center;
	img {
		padding-left: 2em;
		display: block;
		height: 200px;
    margin: 32px;
	}
}

#creative {
  img {
    display: inline;
    float: right;
    width: 123px;
  }
}
#develop {
  img {
    display: inline;
    float: left;
    width: 123px;
    margin: 16px;
  }
}

#monitor {
	display: flex;
	align-items: center;
	justify-content: center;
	background: #fff;
	height: 300px;
	max-width: 500px;
	transform: perspective(135px) rotateX(2deg) rotateY(-5deg) rotateZ(8deg);
	border-radius: 4px;
	color: black;
	overflow: hidden;
	margin: 0 auto;
}
#stripe_0 {
	height: 900px;
	width: 300px;
	background: $light;
	position: absolute;
	top: -500px;
	left: -90px;
	transform: rotateZ(240deg);
}
#stripe_1 {
	height: 900px;
	width: 300px;
	background: $middle;
	position: absolute;
	top: -500px;
	left: -200px;
	transform: rotateZ(240deg);
}
#form_thanks {
  display: none;
}
.full_height_container {
	align-items: center;
	margin: 0 auto;
	width: 100%;
	padding: 30px;
	grid-template-columns: repeat(auto-fit, minmax(360px, 1fr));
}
.middle_container {
  @media (min-width: 576px) {
    display: flex;
  }
}

.container {
  padding: 30px;
  @media (min-width: 576px) {
    max-width: 600px;
    margin: auto;
  }
}
.welcome_message {
	font-size: .7em;
	color: $dark;
	animation: blink 1.6s infinite alternate;
	text-shadow: 0 0 20px $dark;
}
.welcome,
.showcase {
	padding-bottom: 3em;
}

.company_name {
	font-size: 40px;
	color: #dedede;
}
.learn_more {
  display: none;
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	text-align: center;
	animation: blink 1s infinite alternate;
	text-shadow: 0 0 42px $dark;
	text-align: center;
  @media (min-width: 576px) {
		display: block;
	}
	a {
		display: inline-block;
		color: $dark;
		text-decoration: none;
	}
}

.down_arrow {
	display: block;
	margin: 1em auto;
	height: 2em;
	path {
		fill: $glow;
	}
	color: #6ba3a5;
	font-size: 12px;
	text-shadow: 5px 5px 10px #2a3138;
	text-shadow: 0 0 42px $dark;
	animation: blink 1.5s infinite alternate;
}

#contact_submit {
	width: 100%;
}
.contact_button {
	color: #2a3138;
	background: $light;
	border-radius: 8px;
	padding: .5em 1.5em;
	margin-top: 1.5em;
	border: none;
	font-size: 18px;
	&:hover {
		background: $accent;
		color: #fff;
		text-shadow: 1px 1px 2px #121212;
	}
}

#portfolio_container {
  padding: 30px;
  h2 {
    @media (min-width: 576px) {
      text-align: center;
      margin-bottom: 12px;
    }
  }
}
#examples_container {
  display: flex;
  width: 100%;
  #example_2, #example_1 {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    @media (min-width: 576px) {
      width: 50%;
    }
    a { 
      text-align: center;
      img {
        max-width: 80%;
        border-radius: 16px;
        border: solid 1px black;
      }
    }
  }
}

#contact_container, #intro_container {
	padding: 0;
  @media (min-width: 576px) {
		max-width: 700px;
    margin: auto;
	}
}
#intro_container {
  @media (min-width: 576px) {
		max-width: 600px;
    margin: auto;
	}
}
#contact_text {
	padding: 3em;
	background: #fff;
	color: #121212;
	h2 {
		font-size: 2.5em;
    @media (min-width: 576px) {
      font-size: 3em;
    }
	}
}
#contact_us_image {
	height: 128px;
	transform: rotateZ(8deg);
	float: right;
	margin-left: 1em;
}
#contact_form {
	color: #fff;
	padding: 3em;
	input,
	textarea {
		color: #fff;
		width: 100%;
		height: 3em;
		margin-bottom: 8px;
		border-radius: 8px;
		border: solid 1px $middle;
		background: #1a2229;
		padding: 12px;
	}
	textarea {
		padding-top: 4px;
		padding-left: 12px;
		height: 20em;
	}
}

@keyframes blink {
	0% {
	}
	100% {
		text-shadow: 0 0 0px $dark;
	}
}
